@mixin for-phone-only {
  @media (max-width: 1279px) {
    @content;
  }
}

footer {
  background-color: #1a4a66;
  width: 100%;
  height: 342px;
  display: flex;
  flex-direction: row;
  padding: 32px 62px;

  @include for-phone-only {
    flex-direction: column;
    height: fit-content;
    padding: 25px 15px;
    gap: 24px;
  }
}

.box {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: end;
  width: 50%;
  height: auto;
  gap: 20px;

  span {
    font-size: 14px;
    font-weight: 500;
    line-height: 18px;
    color: #b6b6b6;
  }

  @include for-phone-only() {
    width: auto;
    align-items: start;
  }
}

.us {
  color: #ffffff;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  text-align: left;
}

.location {
  display: flex;

  img {
    width: 24px;
  }

  p {
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
    margin-left: 8px;
  }
}

.logos {
  width: 200px;
}

.listcontainer {
  display: flex;
  flex-direction: row;
  width: 50%;
  gap: 30px;
  height: auto;

  @include for-phone-only {
    flex-direction: column;
    gap: 28px;
    width: auto;
  }
}

.lista {
  display: flex;
  flex-direction: column;
  height: auto;

  span {
    font-size: 16px;
    font-weight: 500;
    line-height: 22px;
    margin-bottom: 12px;
    color: #ffffff;

    @include for-phone-only() {
      font-size: 14px;
    }
  }

  p {
    font-size: 16px;
    font-weight: 500;
    line-height: 22px;
    color: #b6b6b6;
    margin-bottom: 8px;
    cursor: pointer;

    @include for-phone-only() {
      font-size: 14px;
    }
  }
}

.linha {
  height: 1px;
  width: 100vw;
  background-color: #3e6f73;
  margin: 0px -15px;
}

.dropdown-spacer {
  width: 100%;
  height: 56px;
}