@mixin desktop {
  @media (min-width: 1280px) {
    @content;
  }
}

.content {
  width: 100%;
  height: auto;
}

.title {
  width: calc(100% - 32px);
  max-width: 600px;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 16px auto;

  @include desktop {
    width: calc(100% - 40px);
    max-width: 1280px;
    margin-bottom: 24px;
    justify-content: left;

    span {
      font-size: 16px;
      font-style: normal;
      font-weight: 600;
      line-height: 24px;
    }
  }

  div {
    width: 100%;
    max-width: 1182px;

    @include desktop {
      display: flex;
      justify-content: space-between;
    }

    button {
      width: 136px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 8px;
      border: 1px solid #1b4b66;
      background: transparent;
      transition: all 0.3s;

      &:hover {
        transform: translateY(-5px);
        background-color: #f0f0f0;
        box-shadow: 2px 2px 8px 2px rgba(0, 0, 0, 0.15);
      }
    }
  }
}

.userDetails {
  width: calc(100% - 32px);
  max-width: 600px;
  height: 112px;
  margin: 0 auto;
  border-radius: 8px;
  background: #f1f1f1;
  padding: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  img {
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 50%;
    flex-shrink: 0;
  }
}

.txtContainer {
  height: 100%;
  padding: 7px 0;
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.menu {
  width: calc(100% - 32px);
  display: flex;
  max-width: 600px;
  flex-direction: column;
  margin: 16px auto 34px auto;

  @include desktop {
    margin: 0;
    width: 271px;
    border-radius: 8px;
    background-color: #f1f1f1;
    padding: 0 7px 0 8px;
    height: 504px;
    position: relative;
  }
}

.item {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;

  @include desktop {
    height: 72px;
  }
}

.logoutButton {
  width: calc(100% - 32px);
  max-width: 600px;
  margin: 0 auto;
  margin-bottom: 16px;
}

.breadcrump {
  width: 100%;
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 20px;
}

.boxContainer {
  width: 100%;
  max-width: 1320px;
  display: flex;
  margin: 0 auto 96px auto;
  padding-left: 20px;
  gap: 23px;
}

.component {
  width: 874px;
  min-height: 967px;
  display: flex;
}

.movingBar {
  width: 4px;
  height: 72px;
  padding: 9px 0;
  position: absolute;
  left: 0;
  transition: all 0.3s;

  &::after {
    display: block;
    content: "";
    border-radius: 8px 0 0 8px;
    background-color: #1b4b66;
    width: 100%;
    height: 54px;
  }
}

.active {
  @include desktop {
    span {
      color: #1b4b66;
    }
  }
}
