body {
  margin: 0px;
  padding: 0px;
  width: 100%;
}

.mycart {
  font-size: 34px;
  font-weight: 600;
  line-height: 44px;
  color: #1b4b66;
  width: 100%;
  max-width: 1280px;
  padding: 0 20px;
  margin: 8px auto 36px auto;
}

.productinfo {
  width: 100%;
  padding: 0 20px;
  max-width: 1280px;
  margin: 0 auto 96px auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.couponContainer {
  display: flex;
  width: 100%;
  background-color: #f1f1f1;
  padding: 18px;
  border-radius: 4px;
  position: relative;
  margin-top: 32px;

  input[type="text"] {
    background-color: #f1f1f1;
    border: none;
    outline: none;
    color: #626262;
  }

  input[type="submit"] {
    background-color: #f1f1f1;
    border: none;
    position: absolute;
    right: 12px;
    top: 0;
    bottom: 0;
  }
}

.table {
  display: flex;
  flex-direction: column;
  min-height: 311px;
}

.separator {
  height: 1px;
  background-color: #0000001f;
  width: auto;
}

.ordersummary {
  display: flex;
  flex-direction: column;
  width: 388px;
  margin: 16px 20px 0 0px;

  h1 {
    margin-bottom: 8px;
  }
}

.pricesContainer {
  margin: 30px 0;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.pricecontainer {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.pname {
  width: 50%;
}

.buttonDrop {
  width: 100%;
  background-color: transparent;
  width: 100%;
  height: 34px;
  border: none;
  display: flex;
  justify-content: space-between;
  align-items: start;
  margin-top: 84px;
  border-bottom: 1px solid #0000001f;
}

.txtpai {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}

.txtfilho {
  display: flex;
  justify-content: space-between;
  width: 277px;
}

.txtcontainer {
  display: flex;
  position: absolute;
  right: 0;
  bottom: 0;
  gap: 4px;

  button {
    width: 136px;
    height: 44px;
    background-color: transparent;
    border: none;
  }
}

.itemsmap {
  display: flex;
  width: auto;
  gap: 78px;
  justify-content: space-between;
  margin-right: 12px;
}

.wishlisttxt {
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
  text-decoration: underline;
  color: #1b4b66;
}

.removetxt {
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
  text-decoration: underline;
  color: #b00020;
}

.btnContinue {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding: 10px 0px;
  border: 2px solid #1b4b66;
  gap: 8px;
  border-radius: 8px;
  background-color: #ffffff;
  color: #1b4b66;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  transition: all 300ms linear;
  cursor: pointer;
}

.btnContinue:hover {
  box-shadow: 6px 6px 6px rgb(27, 75, 102, 0.5);
}

.outlined {
  border: 2px solid #1b4b66;
  background-color: transparent;
  color: #1b4b66;
}

.btnPlace {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding: 10px 0px;
  border: 2px solid #1b4b66;
  gap: 8px;
  border-radius: 8px;
  background-color: #1b4b66;
  color: white;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  transition: all 300ms linear;
  cursor: pointer;
}

.btnPlace:hover {
  box-shadow: 6px 6px 6px rgb(27, 75, 102, 0.5);
}

.btnContainer {
  margin-top: 10px;
  display: flex;
  gap: 32px;
}

.defaultBtn {
  width: 180px;
  height: 40px;
  background-color: #1b4b66;
  border-radius: 8px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
}

.outlined {
  background-color: transparent;
  border: 2px solid #1b4b66;
  color: #1b4b66;
}

.productContainer {
  margin-top: 24px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.product {
  width: 710px;
  height: 99px;
  position: relative;
}

.productData {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.noItem {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto 130px auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 30px;

  img {
    margin-top: 100px;
    width: 300px;
    height: 300px;
  }

  div {
    display: flex;
    flex-direction: column;
    gap: 16px;
    align-items: center;
    text-align: center;
  }

  span {
    width: 75%;
  }
}
